a,img { -webkit-touch-callout: none; }
button { background-color: transparent; border: 0 }
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
